body {
  background-color: #dee7f5;
}

.breed-select {
  min-width: 300px;
  margin: 30px;
  padding: 15px;
  font-size: 20px;
}

.loader {
  color: #7272d3;
  margin: 30px;
  font-size: 25px;
}

.error {
  color: #ff3d00;
  margin: 30px;
  font-size: 25px;
}

.load-logo {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.load-logo:after {
  content: "";
  box-sizing: border-box;
  width: 3px;
  height: 24px;
  background: #7272d3;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  max-width: 90%;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.js-cat-img {
  width: 400px;
  height: 100%;
}

.js-cat-description {
  max-width: 600px;
  margin: 0 auto;
}

